const setParent = object => {
  _.forIn(child => {
    child.parent = object;
    setParent(child);
  }, object.children);
};

const routeConfig = {
  Settings: {
    name: 'Settings',
    optionalParam: ['(/:notSetupDatabase)']
  },
  Report: {
    name: 'Report',
    children: {
      List: {
        name: 'ReportList',
        path: 'list/:reportType(/:catId(/:isGlobal))'
      },
      View: {
        name: 'View',
        path: 'view/:id(/:version)'
      },
      Edit: {
        name: 'ReportsEdit',
        path: 'edit/:id(/:version)'
      }
    }
  },
  Dashboard: {
    name: 'Dashboard',
    children: {
      List: {
        name: 'DashboardList',
        path: 'list/:catId(/:dashboardId(/:isGlobal))'
      },
      New: {
        name: 'NewDashboard',
        path: 'new'
      },
      Edit: {
        name: 'EditDashboard',
        path: 'edit/:id'
      },
      DashboardViewer: {
        name: 'DashboardViewer',
        path: ':id'
      }
    }
  },
  ReportDesigner: {
    name: 'ReportDesigner',
    children: {
      ReportDesignerDetail: {
        name: 'ReportDesignerDetail',
        path: ':reportId'
      }
    }
  },
  ReportViewer: {
    name: 'ReportViewer',
    children: {
      ReportViewerDetail: {
        name: 'ReportViewerDetail',
        path: ':id'
      }
    }
  },
  Viewer: {
    name: 'Viewer',
    children: {
      ReportPart: {
        name: 'ReportPart',
        path: 'reportpart/:id'
      },
      DashboardPart: {
        name: 'DashboardPart',
        path: 'dashboardpart/:id'
      },
      ReportBody: {
        name: 'ReportBody',
        path: 'reportbody/:id'
      },
      Dashboard: {
        name: 'Dashboard',
        path: 'dashboard/:id'
      }
    }
  },
  Account: {
    name: 'account',
    children: {
      Activation: {
        name: 'Activation',
        path: 'activation'
      },
      Login: {
        name: 'Login',
        path: 'login'
      },
      ResetPassword: {
        name: 'ResetPassword',
        path: 'resetPassword'
      },
      ForgotPassword: {
        name: 'ForgotPassword',
        path: 'forgotPassword'
      }
    }
  },
  MyProfile: {
    name: 'MyProfile',
    children: {
      PersonalInfo: {
        name: 'PersonalInfo',
        path: 'personalinfo'
      },
      SecurityOptions: {
        name: 'SecurityOptions',
        path: 'securityoptions'
      }
    }
  }
};

_.forIn(setParent, routeConfig);

// This contents the uri for going back later using router.
const SOURCE_PATH_QUERY_KEY = 'spath';

export default {
  routeConfig,
  mergeRouteConfig: _.mergeWith(
    (objValue, srcValue) => {
      srcValue.path = _.compact(_.concat(objValue, srcValue.optionalParam)).join('');
      return srcValue;
    },
    _,
    routeConfig
  ),
  SOURCE_PATH_QUERY_KEY
};



