/**
 * Invoke AJAX call to path with associated options
 * @param {type} path
 * @param {type} options
 * @returns {Promise}
 */
function ajax(path, options) {
  return new Promise(function (resolve, reject) {
    var xhr = new XMLHttpRequest();

    xhr.open(options.type, path);
    xhr.setRequestHeader("content-type", "application/json");
    xhr.setRequestHeader("access_token", options.headers.access_token);
    var currentTenantId = options.headers.currentTenantId;
    if (!_.isNil(currentTenantId)) {
      xhr.setRequestHeader("current_tenant", options.headers.currentTenantId);
    }
    xhr.setRequestHeader("selected_tenant", options.headers.tenantId);

    xhr.onload = function () {
      if (this.status >= 200 && this.status < 300) {
        resolve(JSON.parse(xhr.response));
      } else {
        let error = new Error();
        error.message = xhr.statusText;
        reject(error);
      }
    };
    xhr.onerror = function () {
      reject({
        status: this.status,
        statusText: xhr.statusText
      });
    };
    xhr.send(options.data);
  });
}

/**
 * Web API data fetcher
 * @constructor
 * @param {string} path - Query url path
 * @param {Object} config - Advanced configuration.
 */
class Fetcher {
  constructor(headers, webApiUrl, config) {
    this.headers = headers;
    this.webApiUrl = webApiUrl;
    this.config = config;
  }

  /**
   * Resolve API url from path
   * @param {string} path
   * @returns {string}
   */
  resolveUrl(path: string) {
    ensure(path || path !== '', 'Path of url must be provided');
    const root = this.webApiUrl;
    const safePath = path ? path[0] === '/' ? path.substring(1) : path : '';
    return root.concat(root.slice(-1) !== '/' ? '/'.concat(safePath) : safePath);
  }

  /**
   * GET requrest
   */
  get(url: String) {
    const path = this.resolveUrl(url);
    return ajax(path, {
      type: 'GET',
      config: this.config,
      headers: this.headers
    });
  };

  /**
   * POST request with payload data
   * @param payload - POST data
   */
  post(url, payload, cacheKey) {
    const path = this.resolveUrl(url);
    return ajax(path, {
      type: 'POST',
      data: payload ? JSON.stringify(payload) : payload,
      config: this.config,
      headers: this.headers
    });
  };

  /**
   * DELETE request with payload data
   * @param payload - DELETE data
   */
  delete (url, payload) {
    const path = this.resolveUrl(url);
    return ajax(path, {
      type: 'DELETE',
      data: payload ? JSON.stringify(payload) : payload,
      config: this.config,
      headers: this.headers
    });
  };
}

export default Fetcher;


