import localizer from 'share/localization/Izenda.Localizer';

export default {
  LICENSE_KEY: {
    Alerting: 'Alerting',
    Form: 'Form',
    Dashboard: 'Dashboard',
    ReportTemplateComponent: 'Report Templates',
    Scheduling: 'Scheduling',
    Exporting: 'Exporting',
    ReportDesigner: 'Report Designer',
    Charting: 'Charting',
    Maps: 'Maps'
  },
  get MESSAGE() {
    return {
      MG02: localizer.getText('LICENSE_CHECKER_MESSAGE_CONNECTION_STRING_INVALID'),
      MG04: localizer.getText('LICENSE_CHECKER_MESSAGE_CONNECT_ERROR'),
      MG05: localizer.getText('LICENSE_CHECKER_MESSAGE_CANNOT_OPEN_DATABASE'),
      MG06: localizer.getText('LICENSE_CHECKER_MESSAGE_LOGIN_FAILED'),
      MG07: localizer.getText('LICENSE_CHECKER_MESSAGE_CONFIRM_OVERRIDE_CONNECTION_STRING'),
      MG08: localizer.getText('LICENSE_CHECKER_MESSAGE_INVALID_CONNECTION_STRING')
    };
  },
  get Description() {
    return localizer.getText('LICENSE_CHECKER_MESSAGE_CONNECTION_STRING_DESCRIPTION');
  },
  SERVER_TYPE: {
    MSSQL: '572bd576-8c92-4901-ab2a-b16e38144813',
    UNKNOWN: '00000000-0000-0000-0000-000000000000'
  },
  CONNECTION_STRING_CONSTRAINT: {
    presence: {
      presence: true,
      get message() {
        return localizer.getText('LICENSE_CHECKER_MESSAGE_CONNECTION_STRING_CONSTRAINT');
      }
    }
  }
};



