/* https://github.com/zertosh/invariant/blob/master/invariant.js */
/**
* Ensure the condition is met. Otherwise, raise an Error with formatted message
*
* Provide sprintf-style format message (only %s is supported) and arguments
* to provide information about what broke and what you were
* expecting.
*
* @param {boolean} condition
* @param {string} msg - Format message
* @param {Array<any>} args - Format arguments
*/
export default function ensure(condition: boolean, msg: string, ...args : Array<any>) {
  if (!condition) {
    let argIndex = 0;
    const error = new Error(
      msg.replace(/%s/g, () => args[argIndex++])
    );

    error.name = 'Pre-check Failed';

    throw error;
  }
}



