import ReportStore from "share/workers/logic/report/Izenda.ReportStore";

self.onmessage = function(e) {
  const {
    type,
    apiInfo,
    apiInfo: { tenantId },
    apiInfo: { headers },
    apiInfo: { webApiUrl }
  } = e.data;

  const store = new ReportStore(tenantId, headers, webApiUrl);
  let promise = {};

  switch (type) {
    case "REPORT_PART_REVIEW_DATA":
      promise = store.loadPreviewData(e.data.requestData);
      break;
    case "REPORT_PART_REVIEW_DATA_CONFIG":
      promise = store.loadConfigReportPartPreviewData(e.data.requestData);
      break;
  }

  promise.then(postMessage);
};



