!(function () {
  var ensure = require('ensure');
  var config = require('Izenda.Config');

  function Localizer() {
    this.activeLanguage = config.DefaultLanguage;
    this.languages = {};
  }

  /**
   * Register language data
   * @param {Array} array of localization keys
   * @param {String} language
   */
  Localizer.prototype.registerLanguage = function (data, language) {
    ensure(data, 'Invalid data');
    ensure(language, 'Language must be set');

    this.languages[language] = data;
  }

  /**
   * Set active language
   * @param {String} language
   */
  Localizer.prototype.setActiveLanguage = function (language) {
    ensure(language, 'Language is not valid');

    var isLanguageSupported = !!_.get(this.activeLanguage, this.languages);

    if (isLanguageSupported) {
      this.activeLanguage = language;
    };
  }

  /**
   * Get localization text in active language
   * @param {type} language
   */
  Localizer.prototype.getText = function (key) {
    var activeLanguage = _.get(this.activeLanguage, this.languages);

    return _.getOr('',key,activeLanguage);
  }

  var singleton = new Localizer();

  module.exports = singleton;
}())



