function buildListFilter(filters) {
  if (_.isEmpty(filters)) {
    return '';
  }

  let result = '<ul style="list-style-type: decimal" >';

  let index = 1;
  _.forEach(filter => {
    const filterString = `<li>${filter.alias}</li>`;
    result += filterString;
    index++;
  }, filters);

  result += '</ ul>';

  return result;
}

