import Fetch from 'share/workers/fetch/Izenda.Fetch';
const ROOT_URL = 'report/';
const FUSION_URL= 'fusion/';

class ReportsDataFetcher {
  constructor(tenantId: String, headers: Array<any>, webApiUrl: String) {
    this.tenantId = tenantId;
    this.fetch = new Fetch(headers, webApiUrl);
  }

  /**
   * Report information
   * @param {String} reportId
   * @param {Object} reportProperty
   * @returns {Promise}
   */
  reportByProperty(reportId: String, reportProperty: Object) {
    return this.fetch.get(
      ROOT_URL + "reportByProperty/" + reportId + "/" + reportProperty
    );
  }

  /**
   * Load all filter field data for list filter
   * @param {Object} data
   * @returns {Promise}
   */
  loadAllFilterFieldsData(data: Object): Object{
    return this.fetch.post(ROOT_URL + "loadAllFilterFieldsData", data);
  }

  /**
   * Fetch definition of report part.
   * @param  {String} id
   */
  loadReportPartDefinition(id: String, reportId: String): Object {
    if (reportId) {
      return this.fetch.get(ROOT_URL + "reportPart/" + id + "/" + reportId);
    }
    return this.fetch.get(ROOT_URL + "reportPart/" + id);
  }

  /**
   * Get list of operator group and its operators
   * @param {string} reportKey
   * @returns {Object}
   */
  loadFilterOperatorGroup(): Object {
    return this.fetch.get(ROOT_URL + "filter/operators");
  }

  /*
   * Load list time periods
   * @returns {promise}
   */
  loadTimePeriods(): Object {
    return this.fetch.get(ROOT_URL + "filter/timeperiods/");
  }

  /**
   * Validate report by report id
   * @param {string} reportId
   * @returns {promise}
   */
  validateReportId(reportId: String): Object {
    return this.fetch.get(ROOT_URL + "isReportValid/" + reportId);
  }

  /**
   * load report part review data on save
   * @param  {Object} data
   */
  loadPreviewData(data: Object): Object {
    return this.fetch.post(FUSION_URL + "loadData", data);
  }

  /**
   * load report part review data in draft report
   * @param  {Object} data
   */
  loadConfigReportPartPreviewData(data: Object): Object {
    return this.fetch.post(FUSION_URL + "reportPartPreview", data);
  }
}

export default ReportsDataFetcher;




